@import url('https://fonts.googleapis.com/css2?family=Pixelify+Sans:wght@400..700&display=swap');

body {
  font-family: "Pixelify Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}

.App {
  display: flex;
  justify-content: center;
  padding: 20px;

}

.boss-table {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 10px;
}

.zone-grid {
  width: 50%;
  /* Each zone takes up half of the container */
  box-sizing: border-box;
  padding: 10px;
}

.boss-zone {
  border-radius: 5px;
  margin-bottom: 20px;
}

.boss-zone h2 {
  margin-top: 0;
}

.bosses {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 10px;
}

.boss {
  display: flex;
  align-items: center;
  border-radius: 5px;
  padding: 10px;
}

.boss img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 10px;
}

.boss-details {
  flex-grow: 1;
}

.boss-details h3 {
  margin-top: 0;
  margin-bottom: 5px;
}

.boss-name {
  font-weight: 500;
  font-size: 16px;
  margin: 0;
}

.boss-time {
  font-weight: 500;
  font-size: 15px;
  color: #666;
  margin: 0;
}

.zone-name {
  font-size: 15px;
  margin: 0;
}